        <h2 className="h2">12. –ù–ï–î–ï–ô–°–¢–í–ò–¢–ï–õ–¨–ù–û–°–¢–¨ –¢–ê–ú, –ì–î–ï –≠–¢–û –ó–ê–ü–†–ï–©–ï–ù–û</h2>
